<template>
  <div class="container">
    <homeTop></homeTop>
    <lunbo></lunbo>
    <ul class="">
      <li v-for="item in cz_array" :key="item.key" @click="clickCell(item.name)" class="li-item clearfix">
        <img v-bind:src=item.image >
      <div class="cp-text">
        <p class="cp-title">{{item.name}}</p>
        <p class="cp-info">{{item.info}}</p>
      </div>
      </li>
    </ul>
    <tabBar v-bind:idx='0'></tabBar>
  </div>
</template>

<script>
import HomeTop from './HomeTop'
import Lunbo from './Lunbo'
import TabBar from './TabBar'
export default {
  name: 'Home',
  data: function () {
    return {
      test: require('../assets/image/fc3d_icon.png'),
      cz_array: [
        {name: '', info: '95%', image: require('../assets/image/ico_lottery_cqssc.png')},
        {name: '', info: '12%', image: require('../assets/image/centralfoot.png')},
        {name: '115', info: '60%', image: require('../assets/image/gd115_icon.png')},
        {name: '', info: '15%5%', image: require('../assets/image/centralbasket.png')},
        {name: '', info: '500', image: require('../assets/image/centralsfc.png')},
        {name: '', info: '2', image: require('../assets/image/centraldcb.png')},
        {name: '', info: '3', image: require('../assets/image/centraldlt.png')},
        {name: '3D', info: '25%', image: require('../assets/image/fc3d_icon.png')},
        {name: '', info: '25%', image: require('../assets/image/centralp3.png')},
        {name: '', info: '15%', image: require('../assets/image/centralrxn.png')},
        {name: '', info: '', image: require('../assets/image/centralbd.png')}

      ]
    }
  },
  methods: {
    clickCell (name) {
      this.$store.commit('setTitle', name)
      if (name === '' || name === '') {
        this.$router.push('SSQ')
      } else if (name === '3D' || name === '') {
        this.$router.push('PL3')
      }
      this.$store.commit('setLotteryName', name)
    }
  },
  components: {
    Lunbo,
    HomeTop,
    TabBar
  }
}
</script>

<style scoped>
@import url("../assets/css/home.css");
ul{
    margin-bottom: 1.333rem;
}
</style>



